import React from "react";
import "./Home.css";
import image from "../images/new-logo-white.png";
import AnimatedCursor from "react-animated-cursor";

const Home = () => {
  return (
    <>
      <AnimatedCursor
        innerSize={8}
        outerSize={25}
        color="743, 74, 111"
        outerAlpha={0.2}
        innerScale={0.7}
        outerScale={5}
        clickables={[
          "a",
          'input[type="text"]',
          'input[type="email"]',
          'input[type="number"]',
          'input[type="submit"]',
          'input[type="image"]',
          "label[for]",
          "select",
          "textarea",
          "button",
          ".link",
        ]}
      />

      <div className="Home">
        <div className="allnav">
          <div className="imgnave">
            <img
              src={image}
              className="imgg img-fluid d-lg-flex d-sm-none d-md-none"
            />
          </div>

          <div className="fw-bold fontnav">
            <ul className="flex  ">
              <li className="fontnavvv">
                <polygon className="my-2">
                  <a href="#Astsharat"> </a>
                </polygon>
              </li>
              <li className="fontnavvv">
                <polygon className="my-2">
                  <a href="#Ourclients"></a>
                </polygon>
              </li>
              <li className="fontnavvv">
                <polygon className="my-2">
                  <a href="#Astsharat"> </a>
                </polygon>
              </li>
              <li className="fontnavvv">
                <polygon className="my-2">
                  <a href="#Slider"></a>
                </polygon>
              </li>
              <li className="fontnavvv">
                <polygon className="my-2">
                  <a href="#Taotheg"></a>
                </polygon>
              </li>
              <li className="fontnavvv ">
                <polygon className="my-2">
                  <a href="#About"> </a>
                </polygon>
              </li>
            </ul>
          </div>
        </div>
        <div className="all-text-home">
          <div>
            <p className="h5 ">  </p>
          </div>
          <div className="my-3 ">
            <h1 className="h1h1">  </h1>
            <h1 className="my-4 h1h1">   </h1>
            <h1 className="h1h1"> </h1>
          </div>
          <div>
            <p className="le"> |  |  |  | </p>
          </div>
        </div>
      </div>
    </>
  );
};

export default Home;
