import React, { useEffect, useState } from "react";
import { Box } from "@mui/material";
import LinearProgress from "@mui/material/LinearProgress";
import "./index.css";
import imagloooding from "./Companet/images/new-logo-white.png";
import "animate.css";

export default function Looding() {
  const [looding, setlooding] = useState(false);
  useEffect(() => {
    setlooding(true);
    setTimeout(() => {
      setlooding(false);
    }, 4000);
  }, []);

  return (
    <>
      {looding && (
        <div>
          <Box sx={{ width: "100%" }}>
            <LinearProgress color="inherit" sx={{ color: "#665D5A" }} />
          </Box>
          <div className="alllogding">
            <div className="imglogdin animate__bounceInUp">
              <div>
                <img src={imagloooding} />
              </div>
              <div>
                <h2>Designed by: Mostafa Safi</h2>
              </div>
            </div>
            <div className="lodbac"></div>
          </div>
        </div>
      )}
    </>
  );
}
