import { useEffect, useState } from "react";
import {
  Looding,
  Angazat,
  Astsharat,
  About,
  End,
  Footer,
  Help,
  Home,
  Morafat,
  Ourclients,
  Slider,
  Taotheg,
} from "./componets";

function App() {
  const [looding, setlooding] = useState(false);
  useEffect(() => {
    setlooding(true);
    setTimeout(() => {
      setlooding(false);
    }, 4000);
  }, []);

  return (
    <>
      {looding ? (
        <Looding />
      ) : (
        <div>
          <Home /> <About /> <Angazat /> <Taotheg /> <Slider /> <Morafat />
          <Astsharat /> <Ourclients /> <Help /> <End /> <Footer />
        </div>
      )}
    </>
  );
}

export default App;
