import React from "react";
import "./Ourclients.css";
import { styled } from "@mui/material/styles";
import Box from "@mui/material/Box";
import Paper from "@mui/material/Paper";
import Grid from "@mui/material/Grid";
import {
  image1,
  image2,
  image3,
  image4,
  image5,
  image6,
  image7,
  image8,
  image9,
  image10,
  image11,
  image12,
  image13,
  image14,
  image15,
} from "./imageourlinets";
import { Container } from "react-bootstrap";

const Ourclients = () => {
  const Item = styled(Paper)(({ theme }) => ({
    backgroundColor: theme.palette.mode === "dark" ? "#1A2027" : "#fff",
    ...theme.typography.body2,
    padding: theme.spacing(1),
    textAlign: "center",
    color: theme.palette.text.secondary,
  }));
  return (
    <>
      <div className="Ourclients py-5 " id="Ourclients">
        <div data-aos="fade-down" className="container df ">
          <div className="textOurclients">
            <h1></h1>
          </div>
          <Grid container spacing={1}>
            <Grid item lg={2.4} md={4} sm={6}>
              <img src={image1} width="100%" />
            </Grid>
            <Grid item lg={2.4} md={4} sm={6}>
              <img src={image2} width="100%" />
            </Grid>
            <Grid item lg={2.4} md={4} sm={6}>
              <img src={image3} width="100%" />
            </Grid>
            <Grid item lg={2.4} md={4} sm={6}>
              <img src={image4} width="100%" />
            </Grid>
            <Grid item lg={2.4} md={4} sm={6}>
              <img src={image5} width="100%" />
            </Grid>
            <Grid item lg={2.4} md={4} sm={6}>
              <img src={image6} width="100%" />
            </Grid>
            <Grid item lg={2.4} md={4} sm={6}>
              <img src={image7} width="100%" />
            </Grid>
            <Grid item lg={2.4} md={4} sm={6}>
              <img src={image8} width="100%" />
            </Grid>
            <Grid item lg={2.4} md={4} sm={6}>
              <img src={image9} width="100%" />
            </Grid>
            <Grid item lg={2.4} md={4} sm={6}>
              <img src={image10} width="100%" />
            </Grid>
            <Grid item lg={2.4} md={4} sm={6}>
              <img src={image11} width="100%" />
            </Grid>
            <Grid item lg={2.4} md={4} sm={6}>
              <img src={image12} width="100%" />
            </Grid>
            <Grid item lg={2.4} md={4} sm={6}>
              <img src={image13} width="100%" />
            </Grid>
            <Grid item lg={2.4} md={4} sm={6}>
              <img src={image14} width="100%" />
            </Grid>
            <Grid item lg={2.4} md={4} sm={6}>
              <img src={image15} width="100%" />
            </Grid>
          </Grid>
        </div>
      </div>
    </>
  );
};

export default Ourclients;
