import React from "react";
import "./Astsharat.css";
import image2 from "../images/vert-border.png";
import imagebutoon from "../images/wave-haikei.png";

import Box from "@mui/material/Box";
import Modal from "@mui/material/Modal";
// images viduo
import image1 from "../images/vidu4.jpg";
import images2 from "../images/vidu5.jpg";
import image3 from "../images/viduio3.jpg";
import image4 from "../images/viduo1.jpg";
import image5 from "../images/vivdu2.jpg";
const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 700,
  border: "2px solid #000",
  boxShadow: 24,
};

const Astsharat = () => {
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  /*  */
  const [opentow, setopentow] = React.useState(false);
  const handleOpentow = () => setopentow(true);
  const handleClosentow = () => setopentow(false);
  /*  */
  const [opentree, setopentree] = React.useState(false);
  const handleOpentree = () => setopentree(true);
  const handleClosentree = () => setopentree(false);
  /*  */
  const [openfore, setopenfore] = React.useState(false);
  const handleOpenfore = () => setopenfore(true);
  const handleClosenfore = () => setopenfore(false);
  /*  */
  const [openfave, setopenfave] = React.useState(false);
  const handleOpenfave = () => setopenfave(true);
  const handleClosenfave = () => setopenfave(false);

  return (
    <>
      <div className="Astsharat " id="Astsharat">
        <div className=" text-white container">
          <div className=" text-center  zed  ">
            <div data-aos="fade-down">
              <img src={image2} />
            </div>
            <div data-aos="fade-up ">
              <h1 className="pt-3"> </h1>
              <p className="pt-4  w-75  m-auto lihat ">
                         
                         
                         
                         
                       .
              </p>
            </div>
          </div>
          <div className="row pt-5">
            <div className="col-lg-4 col-md-12 text-center text-white py-4">
              <div className="poss" onClick={handleOpen}>
                <img src={image5} />
                <span uk-icon="play"></span>
              </div>
              <Modal open={open} onClose={handleClose}>
                <Box sx={style}>
                  <iframe
                    width="700"
                    height="400"
                    src="https://www.youtube-nocookie.com/embed/imaNONHbUw0?controls=0"
                    title="YouTube video player"
                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
                  ></iframe>
                </Box>
              </Modal>
              <div>
                <p className="pt-3 bony">
                           
                </p>
              </div>
            </div>
            <div className="col-lg-4 col-md-12 text-center text-white py-4">
              <div className="poss" onClick={handleOpentow}>
                <img src={image4} />

                <span uk-icon="play"></span>
              </div>
              <Modal
                open={opentow}
                onClose={handleClosentow}
                aria-labelledby="modal-modal-title"
                aria-describedby="modal-modal-description"
              >
                <Box sx={style}>
                  <iframe
                    width="700"
                    height="400"
                    src="https://www.youtube-nocookie.com/embed/hcU8GvH4zCQ?controls=0"
                    title="YouTube video player"
                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
                    allowFullScreen
                  ></iframe>
                </Box>
              </Modal>
              <div className="text-center">
                <p className="pt-3 bony">   </p>
              </div>
            </div>
            <div className="col-lg-4 col-md-12 text-center text-white py-4">
              <div className="poss" onClick={handleOpentree}>
                <img src={image3} />

                <span uk-icon="play"></span>
              </div>
              <Modal
                open={opentree}
                onClose={handleClosentree}
                aria-labelledby="modal-modal-title"
                aria-describedby="modal-modal-description"
              >
                <Box sx={style}>
                  <iframe
                    width="700"
                    src="https://www.youtube.com/embed/cM4e9V1RTtE"
                    height="400"
                    title="YouTube video player"
                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
                    allowFullScreen
                  ></iframe>
                </Box>
              </Modal>
              <div className="text-center">
                <p className="pt-3 bony">    </p>
              </div>
            </div>
          </div>
          <div className="row">
            <div className="col-lg-6 col-md-12 text-center text-white py-4">
              <div className="poss" onClick={handleOpenfore}>
                <img src={image1} />
                <span uk-icon="play"></span>
              </div>
              <Modal open={openfore} onClose={handleClosenfore}>
                <Box sx={style}>
                  <iframe
                    width="700"
                    height="400"
                    src="https://www.youtube.com/embed/EZJkK_GVlPQ"
                    title="YouTube video player"
                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
                  ></iframe>
                </Box>
              </Modal>
              <div>
                <p className="pt-3 bony">  </p>
              </div>
            </div>
            <div className="col-lg-6 col-md-12 text-center text-white py-4">
              <div className="poss" onClick={handleOpenfave}>
                <img src={images2} />
                <span uk-icon="play"></span>
              </div>
              <Modal open={openfave} onClose={handleClosenfave}>
                <Box sx={style}>
                  <iframe
                    width="700"
                    height="400"
                    src="https://www.youtube.com/embed/fUM29O3Xzdc?controls=0"
                    title="YouTube video player"
                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
                  ></iframe>
                </Box>
              </Modal>
              <div>
                <p className="pt-3 bony">   </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Astsharat;
