import React, { useState } from "react";
import "./Angazat.css";
import image2 from "../images/vert-border.png";
import CountUp from "react-countup";
import ScrollTrigger from "react-scroll-trigger";
const Angazat = () => {
  const [counter, setcounter] = useState(false);

  return (
    <>
      <div className="Angazat ">
        <div className="seve">
          <div class="custom-shape-divider-top-1673649426">
            <svg
              data-name="Layer 1"
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 1200 120"
              preserveAspectRatio="none"
            >
              <path
                d="M1200 0L0 0 598.97 114.72 1200 0z"
                class="shape-fill"
              ></path>
            </svg>
          </div>
        </div>
        <div className="post pt-5">
          <div className=" text-white pt-5">
            <div className=" text-center pt-5 w-50 m-auto ">
              <img src={image2} className="pt-5" />
              <div>
                <h1 className="pt-3"> </h1>
                <h1>    </h1>
                <p className="pt-4 pangaz">
                           
                     .      
                          
                     !
                </p>
              </div>
            </div>
            <div className=" container pt-5 ">
              <ScrollTrigger
                onEnter={() => {
                  setcounter(true);
                }}
                onExit={() => {
                  setcounter(false);
                }}
              >
                <div className="row pt-sm-5 ">
                  <div className=" col-md-3  mb-sm-4 col-sm-12  text-center  ">
                    <h1 className="my-4">
                      {counter && <CountUp delay={0} end={500} duration={2} />}
                    </h1>
                    <p className="palag">  500 </p>
                    <p className="palag">  </p>
                  </div>

                  <div className="borderr borderrr mb-sm-4 col-md-3 col-sm-12  text-center  ">
                    <h1 className="d-felx  ">
                      <h1 className="my-4 flexxx">
                        <div></div>
                        {counter && <CountUp delay={0} end={1} duration={2} />}
                      </h1>
                    </h1>
                    <p className="palag">   </p>
                    <p className="palag">
                           
                    </p>
                  </div>

                  <div className="borderr borderrr mb-sm-4 col-md-3 col-sm-12  text-center ">
                    <h1 className="my-4">
                      {counter && <CountUp delay={0} end={3300} duration={2} />}
                    </h1>
                    <p className="palag">  3300  </p>
                    <p className="palag">
                         
                    </p>
                  </div>
                  <div className="mb-sm-4 col-md-3 col-sm-12 borderrr  text-center ">
                    <h1 className="my-4">
                      {counter && <CountUp delay={0} end={12} duration={2} />}
                    </h1>
                    <p className="palag">  12 </p>
                    <p className="palag ">   </p>
                  </div>
                </div>
              </ScrollTrigger>
            </div>
          </div>
          <div className="botnangz m-0 p-0"></div>
        </div>
      </div>
    </>
  );
};

export default Angazat;
